.shops {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-self: stretch;
    flex: 1 0 0;
}

.buttons {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.buttons button {
    border-radius: 0.3rem;
    background: var(--Grayscale-Lightest, #F3F2F1);
    color: #606060;
    padding: 1rem 2rem;
}

button.active {
    background: var(--Main-Secondary, #70C05B);
    color: #FFF;
}

.map {
    width: 100%;
}

 