.reviewsContainer {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 1rem;
width: 100%;
}

.headerContainer {
    display: flex;
    height: 2rem;
    align-items: center;
    gap: 1rem;
    align-self: stretch;
}

.title {
    color: var(--Main-onSurface, #414141);

    /* Desc/Text-Bold/M */
    font-family: Rubik;
    font-size: 1.125rem;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 1.6875rem */
}

.starsContainer {
    display: flex;
align-items: center;

gap: 0.25rem;
}

.textContainer {
    width: 100%;
}

.textContainer textarea{
    width: 100%;
    display: flex;
height: 6.25rem;
padding: 0.5rem 1rem;
align-items: center;
align-self: stretch;
border-radius: 0.25rem;
border: 1px solid var(--Grayscale-Light, #BFBFBF);
background: var(--Main-Surface, #FFF);
color: var(--Main-onSurface, #414141);

/* Desc/Text/S */
font-family: Rubik;
font-size: 1rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 1.5rem */
}

.buttonContainer {
    cursor: pointer;
    display: flex;
width: 11.75rem;
padding: 0.5rem;
justify-content: center;
align-items: center;
gap: 0.5rem;
border-radius: 0.25rem;
background: var(--Main-PrimaryMuted, #FCD5BA);
}

.buttonContainer button {
    display: flex;
height: 1.5rem;
flex-direction: column;
justify-content: center;
    color: var(--Main-Primary, #F63);
text-align: center;

/* Desc/Text/S */
font-family: Rubik;
font-size: 1rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 1.5rem */
}