.order {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    margin-bottom: 7.5rem;
}

.orderButton {
    margin: auto;
    display: flex;
    height: 3.75rem;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Grayscale-Lightest, #F3F2F1);
    margin-top: 3.75rem;
}

.header {
    display: flex;
align-items: center;
gap: 1.5rem;
flex: 1 0 0;
justify-content: space-between;
}
.datetime {
    display: flex;
    gap: 10px;
}

.date,
.time {
    color: var(--Main-onSurface, #414141);

/* Desc / Text-Bold / L */
font-family: Rubik;
font-size: 1.5rem;
font-style: normal;
font-weight: 700;
line-height: 150%; /* 2.25rem */
}
.result {
    display: flex;
padding: 0.25rem 0.5rem;
align-items: flex-start;
gap: 0.625rem;
color: var(--Main-onSurface, #414141);
text-align: center;

/* Desc/Text/S */
font-family: Rubik;
font-size: 1rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 1.5rem */
}

.received {
    border-radius: 0.25rem;
    background: var(--Main-Secondary, #70C05B);
    color: var(--Pallete-onError, #F2F2F2);
}

.return {
    border-radius: 0.25rem;
background: var(--Pallete-Error, #D80000);
color: var(--Pallete-onError, #F2F2F2);
}

.allPrice {
    color: var(--Main-onSurface, #414141);

/* Desc/Text/L */
font-family: Rubik;
font-size: 1.5rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 2.25rem */
}
.btns {
    display: flex;
width: 12.5rem;
padding: 0.5rem;
justify-content: center;
align-items: center;
gap: 0.5rem;
border-radius: 0.25rem;
background: var(--Main-Secondary, #70C05B);
}

.btns button {
border-radius: 0.25rem;
background: var(--Main-Secondary, #70C05B);

/* Desc/Text/S */
font-family: Rubik;
font-size: 1rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 1.5rem */
}
.body {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2.5rem;
    align-self: stretch;
}
.bodyCards {
    display: flex;
align-items: flex-start;
gap: 2.5rem;
align-self: stretch;
flex-wrap: wrap;
}
.bodyButton {
    display: flex;
    width: 12.5rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
background: var(--Grayscale-Lightest, #F3F2F1);
cursor: pointer;
}

.bodyButton button {
    color: var(--Grayscale-Hardest, #606060);
    text-align: center;
    
    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.5rem */
}