.login {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(252, 213, 186, 0.80);
    z-index: 1000;
    display: flex;
    backdrop-filter: blur(10px);
}

.wrapperForm {
    margin: auto;
    display: flex;
    width: 26.25rem;
    flex-direction: column;
    align-items: flex-end;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: #FFF;
    box-shadow: 8px 16px 32px 0px rgba(255, 102, 51, 0.20);
    position: relative;
}

.notFound {
    background-color: red;
    color: white;
    width: 75%;
    text-align: center;
    padding: 5px;
}

.close {
    cursor: pointer;
    display: flex;
    width: 2.5rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    position: relative;
    right: 0;
    top: 0;
}

.form {
    display: flex;
    padding-bottom: 2.5rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    align-self: stretch;
}

.formTitle {
    color: var(--Main-onSurface, #414141);
    text-align: center;
    
    /* Desc/Header/S */
    font-family: Rubik;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 2.25rem */
}

.formTel {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 75%;
}

.formTel label {
    color: var(--Grayscale-Hard, #8F8F8F);
}

.formTel input {
    height: 30px;
    width: 100%;
    font-size: 1.2rem;
}

input {
    border-bottom: 1px solid #8F8F8F;
}

.formLogin {
    width: 75%;
        
}

.formLogin button {
    cursor: pointer;
    display: flex;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Main-PrimaryMuted, #FCD5BA);
    color: var(--Main-Primary, #F63);
    text-align: center;
    width: 100%;
}

.formBtns {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 75%;
}

.formBtns button {
    width: 50%;
    display: flex;
    height: 3rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
}

.formBtns button:first-child {
    flex: 1 0 0;
    border: 1px solid #70C05B;
    color: #70C05B;
    text-align: center;
}

.formBtns button:last-child {
    flex: 1 0 0;
    color: #606060;
    text-align: center;
}

.formTel label{
    margin-bottom: 0.3rem;
}

.formTel input{
    margin-bottom: 1rem;
    padding: 0 5px;
}

@media screen and (max-width: 27em) {
    .login input {
        border: 1px solid grey;
        border-radius: 0.25rem;
        box-shadow: 0px 18px 20px -14px;
    }

    
}