.basketContainer {
    display: flex;
    align-items: flex-start;
    gap: 2.5rem;
    flex: 1 0 0;
}

.deliverySection {
    flex-direction: column;
    display: flex;
    gap: 3rem;
}

.basketContainer hr {

    background-color: black;
}
.leftSide {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.25rem;
    flex: 1 0 0;
    align-self: stretch;
}
.leftHeader {
    display: flex;
    align-items: flex-start;
    gap: 3.125rem;
    align-self: stretch;
}
.allDelete {
    display: flex;
    width: 8.125rem;
    height: 2rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
}
.checkbox {
    display: flex;
    width: 1.5rem;
    height: 1.5rem;
    padding: 0.25rem;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 0.25rem;
    border: 1px solid var(--Grayscale-Lightest, #F3F2F1);
    background: var(--Main-Secondary, #70C05B);
}
.deleteChecked button {
    display: flex;
    width: 8.375rem;
    height: 2rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    border: 0px solid var(--Main-Primary, #F63);
    color: var(--Main-Primary, #F63);
    text-align: center;
}
.products {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 3.125rem;
    flex: 1 0 0;
    align-self: stretch;
}

.rightSide {
    display: flex;
    align-items: flex-start;
    align-self: stretch;
    flex-direction: column;
    gap: 2rem;
}
.bonus {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 1.25rem;
}
.bonusCard {
    display: flex;
align-items: center;
gap: 0.625rem;
}
.description {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 0.625rem;
width: 100%;
}
.descriptionProduct {
    display: flex;
align-items: center;
justify-content: space-between;
align-self: stretch;
}
.descriptionPromo {
    display: flex;
align-items: center;
justify-content: space-between;
align-self: stretch;
}
.result {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.resutlSum {
    display: flex;
    align-items: center;
    align-self: stretch;
    justify-content: space-between;
}
.resultBonus {
    display: flex;
    height: 2rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    color: var(--Main-Secondary, #70C05B);
    text-align: center;
}
.btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
    gap: 1rem;
}
.min {
    display: flex;
    padding: 0.5rem;
    align-items: flex-start;
    gap: 0.625rem;
    align-self: stretch;
    border-radius: 0.25rem;
    background: #D80000;
    color: #FFF;
    font-weight: bold;

}
.order {
    display: flex;
    flex: 1 0 0;
    align-self: stretch;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: #FCD5BA;
    color: #F63;
}

.where,
.when,
.entrance {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.where input,
.where select,
.when input,
.when select,
.entrance select,
.entrance input {
    display: flex;
    padding: 0.5rem 1rem;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
    border-radius: 0.25rem;
    border: 1px solid var(--Grayscale-Light, #BFBFBF);
    background: var(--Main-Surface, #FFF);
}

.point select {
    width: 100%;
}

.home input,
.kvartira input,
.dop input {
    width: 3.3125rem;
}

.home div:first-child,
.kvartira div:first-child,
.home div:first-child,
.point div:first-child,
.street div:first-child,
.dop div:first-child,
.date div:first-child,
.time div:first-child
 {
    color: var(--Grayscale-Hard, #8F8F8F);

}

.when  {
    align-items: center;
}

.when select{
    width: 100%;
}

.code {
    display: flex;
    width: 10.5rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Main-Primary, #F63);
    color: var(--Main-onPrimary, #FFF);
    text-align: center;
}

.inputEmail {
    display: flex;
    width: 10.5rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    border: 0px solid var(--Main-Secondary, #70C05B);
    color: var(--Main-Secondary, #70C05B);
    text-align: center;
}

.checkOnSite {
    display: flex;
    width: 17rem;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Main-Primary, #F63);
    color: var(--Main-onPrimary, #FFF);
    text-align: center;
}

.checkOnGet
 {
    display: flex;
    width: 17rem;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Main-Secondary, #70C05B);
    color: var(--Main-onPrimary, #FFF);
    text-align: center;
}

.time div:last-child {
    position: relative;
}

.when {
}

@media screen and (max-width: 48em) {
    .products {
        align-self: flex-start;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .basketContainer {
        flex-direction: column-reverse;
    }

    .rightSide {
        gap: 1rem;
    }

    .result {
        gap: 1rem;
    }

    .checkOnGet,
    .checkOnSite {
        align-self: stretch;
        width: 100%;
    }
}