@import url('https://fonts.googleapis.com/css2?family=Inter&family=Rubik&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: transparent;
    border: none;
    outline: none;
    text-decoration: none;
    font-family: Rubik, sans-serif;
    font-size: 1vw;
    font-style: normal;
}

.productPage {
    gap: 5rem;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 48em) {
    * {
        font-size: 1.5vw;
    }
}

@media screen and (max-width: 27em) {
    * {
        font-size: 3vw;
    }
}

@media screen and (max-width: 20em) {
    * {
        font-size: 3vw;
    }
}

img {
    height: 1.5em;
}

button {
    cursor: pointer;
}




/* CONTENT */

.main {
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.middleContainer {
    padding: 5.02rem 7.25rem;
}

@media screen and (max-width: 48em) {
    .middleContainer {
        padding: 2.51rem 3.625rem;
    }
}

@media screen and (max-width: 27em) {
    .middleContainer {
        padding: 1.255rem 0rem;
    }
}

@media screen and (max-width: 20em) {
    .middleContainer {
        padding: 0.6275rem 0rem;
    }
}

/* STOCK */
.block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2.5rem;
    align-self: stretch;
    padding: 10px;
}

.block-header {
    display: flex;
    align-items: flex-end;
    gap: 2.5rem;
    align-self: stretch;
}

.block-header__title {
    flex: 1 0 0;
    color: var(--Main-onSurface, #414141);

    /* Desc/Header/M */
    font-family: Rubik;
    font-size: 2.25rem;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 3.375rem */
}

.block-header__button button{
    display: flex;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    color: var(--Grayscale-Hardest, #606060);
    text-align: center;

    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.5rem */
    }

.block-main {
    display: flex;
    align-items: flex-start;
    gap: 2.5rem;
    align-self: stretch;
    flex-wrap: wrap;
}

.card-product {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 0 0;
    border-radius: 0.25rem;
    background: var(--Main-Surface, #FFF);

    /* Default/XS */
    box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.10);
}

.card-product-img {
    display: flex;
    justify-content: center;
    height: 10rem;
    align-self: stretch;
    position: relative;
}

.promotional-price {
    position: absolute;
    left: 0;
    bottom: 0;
    display: inline-flex;
    padding: 0.25rem 0.5rem;
    align-items: flex-start;
    gap: 0.625rem;
    border-radius: 0.25rem;
    background: var(--Main-Primary, #F63);
}

.favourite {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    height: 2rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    flex-shrink: 0;
    border-radius: 0.25rem;
    opacity: 0.5;
}

.card-product-content {
    display: flex;
    padding: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
}
.card-product-content__price {
    display: flex;
    align-items: flex-end;
    align-self: stretch;
    gap: 5px;
    font-weight: bolder;
}

.card-product-content__description {
    align-self: stretch;
    color: var(--Main-onSurface, #414141);

    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.5rem */
}

.card-product-content__rate {
    display: flex;
    align-items: flex-start;
    gap: 0.25rem;
    align-self: stretch;
}

.card-product-content__button{
    display: flex;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    align-self: stretch;
    border-radius: 0.25rem;
    border: 1px solid var(--Main-Secondary, #70C05B);
}

.card-product-content__button:hover {
    border: 1px solid var(--Main-Secondary, #F63);
}

.card-product-content__button:hover,
.card-product-content__button:hover button {
    background: var(--Main-Primary, #F63);
    color: var(--Main-onPrimary, #FFF);
}

.card-product-content__button button{
    display: flex;
    justify-content: center;
    flex: 1 0 0;
    color: var(--Main-Secondary, #70C05B);

    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
}


/* CATALOG */

.catalog {
    display: inline-flex;
flex-direction: column;
gap: 3.75rem;
transition: all 1s ease-out;
padding: 5rem 5.5rem;

}

.catalog-header {
    color: var(--Main-onSurface, #414141);

    /* Desc/Header/XL */
    font-family: Rubik;
    font-size: 4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 6rem */
}

/* CATALOG PRODUCT */



.catalogProduct-filter-price__range {
    height: 1.5rem;
    align-self: stretch;
    position: relative;
}

.catalogProduct-filter-price__range input {
    width: 45%;
}

.catalogProduct-filter-price__range input:last-child {
    position: absolute;
    right: 0;
}

.catalogProduct-filter-stock {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;
}



.catalogProduct-filter-button {
    display: flex;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    align-self: stretch;
    border-radius: 0.25rem;
    background: var(--Main-Primary, #F63);
}

.catalogProduct-filter-button button {
    display: flex;
height: 1.5rem;
flex-direction: column;
justify-content: center;
flex: 1 0 0;
color: var(--Main-onPrimary, #FFF);
text-align: center;

/* Desc/Text/S */
font-family: Rubik;
font-size: 1rem;
font-style: normal;
font-weight: 400;
line-height: 150%; /* 1.5rem */
}

.catalogProduct-panel {
    display: flex;
flex-direction: column;
align-items: center;
gap: 2.5rem;
}
.catalogProduct-panel-filters {
    display: flex;
    align-items: flex-start;
    gap: 1.5rem;
    align-self: stretch;
}
.catalogProduct-panel-cards {
    display: flex;
align-items: flex-start;
gap: 2.5rem;
flex-wrap: wrap;

}
.catalogProduct-panel-button {
    display: flex;
width: 9.8125rem;
padding: 0.5rem;
justify-content: center;
align-items: center;
gap: 0.5rem;
border-radius: 0.25rem;
background: var(--Grayscale-Lightest, #F3F2F1);
}

.catalogProduct-panel-button button {
    display: flex;
height: 1.5rem;
flex-direction: column;
justify-content: center;
flex: 1 0 0;
    color: var(--Grayscale-Hardest, #606060);
    text-align: center;
    
    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.5rem */
}

.panel-filters {
    height: 2rem;
    padding: 0.5rem;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Main-Secondary, #70C05B);
    display: flex;
    justify-content: center;
    color: var(--Main-onSecondary, #FFF);
    text-align: center;

    /* Desc/Text/XS */
    font-family: Rubik;
    font-size: 0.75rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.125rem */
}

.clear-filters {
    height: 2rem;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: var(--Grayscale-Lightest, #F3F2F1);
    display: flex;
    justify-content: center;
    color: var(--Grayscale-Hardest, #606060);
    text-align: center;

    /* Desc/Text/XS */
    font-family: Rubik;
    font-size: 0.75rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.125rem */
}

.catalogProduct-panel-pagination {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.catalogProduct-panel-pagination__page {
    color: var(--Grayscale-Hardest, #606060);
    text-align: center;

    /* Desc/Text/S */
    font-family: Rubik;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 1.5rem */
    font-size: bolder;
}

.catalogProduct-panel-pagination__page:hover {
    color: var(--Main-Primary, #F63);
}

.arrows {
    color: var(--Grayscale-Hardest, #606060);
    cursor: pointer;
}

.arrows:hover path{
    fill: #F63;
}



/* RADIO BUTTONS -BUTTON */

.form_radio_btn {
	display: inline-block;
	margin-right: 10px;
    position: relative;
}
.form_radio_btn input[type=radio] {
	display: none;
}
.form_radio_btn label {
	display: inline-block;
	cursor: pointer;
	padding: 0px 15px;
	line-height: 34px;
	border-radius: 6px;
	user-select: none;
    color: #BFBFBF;
    text-align: center;
    
}
 
/* Checked */
.form_radio_btn input[type=radio]:checked + label {
	background: #70C05B;
    color: #FFF;
    text-align: center;
}
 
/* Hover */
.form_radio_btn label:hover {
	color: #666;
}
 
/* Disabled */
.form_radio_btn input[type=radio]:disabled + label {
	background: #efefef;
	color: #666;
}

.form_radio_btn span {
    display: none;
}

.form_radio_btn:hover input[type=radio]:disabled ~ span {
    position: absolute;
    z-index: 10;
    gap: 10px;
    height: auto;
    width: 100%;
    top: -50%;
    display: flex;
    align-items: center;
    padding: 0.2rem;
    border-radius: 0.5rem;
    background: #F4F6FB;
}   

